.mainAboutPage {
    width: 100%;
    height: 130vh;
    background: linear-gradient(90deg, rgba(255, 255, 255, 1) 53%, rgb(72 82 255) 53%);
    display: flex;
    align-items: center;
    justify-content: center;
}

.aboutpage {
    width: 80%;
    display: flex;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-shadow: 2px 1px 10px black;
}

.aboutpage>h1 {
    text-align: center;
    color: tomato;
}

.aboutpage>div {
    display: flex;
    align-items: center;
    justify-content: center;
}

.aboutpage>div>div {
    width: 50%;
    padding: 2vmax;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.aboutpage>div>div:first-child>p {
    font: 300 1.5vmax "roboto";
    color: rgb(64 64 64 / 82%);
    text-align: center;
}

.aboutpage>div>div:last-child>h1 {
    color: rgb(64 64 64 / 82%);
    margin: 0;
    margin-bottom: 20px;
}

.profileimg {
    border-radius: 100%;
    width: 200px;
    height: 200px;
}

.linkImage {
    height: 60px;
    margin: 10px;
    transition: all 0.5s;
}

.linkImage:hover {
    transform: scale(1.1);
}

@media (max-width: 426px) {
    .aboutpage>div {
        display: block;
    }

    .aboutpage>div>div {
        width: auto;
    }

    .profileimg {
        width: 150px;
        height: 150px;
    }

    .aboutpage>div>div:first-child>p {
        font-size: 3vmax;
    }

    .linkImage {
        width: 60px;
        height: 40px;
    }
}