.confirmOrderPage {
    height: 100vh;
    background-color: white;
    display: grid;
    grid-template-columns: 6fr 3fr;
}

.confirmOrderPage>div:last-child {
    border-left: 1px solid rgba(0, 0, 0, 0.247);
}

.confirmShippingArea {
    padding: 2vmax;
    padding-bottom: 0%;
}

.confirmShippingArea>p {
    font: 400 1.8vmax "Roboto";
}

.confirmShippingAreaBox,
.confirmCartItemsContainer {
    margin: 2vmax;
}

.confirmShippingAreaBox>div {
    display: flex;
    align-items: center;
}

.confirmShippingAreaBox>div>p {
    font: 400 1vmax "Roboto";
    color: black;
}

.confirmShippingAreaBox>div>span {
    color: #575757;
    font: 100 1vmax "Roboto";
    margin: 0 1vmax;
}

.confirmCartItems>p {
    font: 400 1.8vmax "Roboto";
}

.confirmCartItems {
    padding: 2vmax;
    padding-top: 2vmax;
}

.confirmCartItemsContainer {
    max-height: 20vmax;
    overflow-y: auto;
}

.confirmCartItemsContainer>div {
    display: flex;
    font: 400 1vmax "Roboto";
    align-items: center;
    margin: 2vmax 0;
}

.confirmCartItemsContainer>div>img {
    width: 3vmax;
}

.confirmCartItemsContainer>div>a {
    width: 60%;
    text-decoration: none;
    color: #575757;
    margin: 0 2vmax;
}

.confirmCartItemsContainer>div>span {
    font: 100 1vmax "Roboto";
    color: #5e5e5e;
}

.orderSummary {
    padding: 7vmax;
}

.orderSummary>p {
    text-align: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.267);
    box-sizing: border-box;
    font: 400 1.8vmax "Roboto";
    padding: 1vmax;
    width: 100%;
    margin: auto;
}

.orderSummary>div>div {
    display: flex;
    align-items: center;
    font: 300 1vmax "Roboto";
    justify-content: space-between;
}

.orderSummary>div>div>span {
    color: rgba(0, 0, 0, 0.692);
}

.orderSummaryTotal {
    display: flex;
    align-items: center;
    font: 300 1vmax "Roboto";
    justify-content: space-between;
    border-top: 1px solid rgba(0, 0, 0, 0.363);
    padding: 2vmax 0;
}

.orderSummary>button {
    background-color: tomato;
    color: white;
    width: 100%;
    padding: 1vmax;
    border: none;
    margin: auto;
    cursor: pointer;
    transition: all 0.5s;
    font: 400 1vmax "Roboto";
}

.orderSummary>button:hover {
    background-color: rgb(192, 71, 50);
}

@media (max-width: 769px) {
    .confirmShippingArea>p {
        font-size: 2.5vmax;
    }

    .confirmShippingAreaBox>div>p {
        font-size: 1.8vmax;
    }

    .confirmShippingAreaBox>div>span {
        font-size: 1.8vmax;
    }

    .confirmCartItems>p {
        font-size: 2vmax;
    }

    .confirmCartItemsContainer>div>img {
        width: 5vmax;
    }

    .confirmCartItemsContainer>div>a {
        font-size: 1.5vmax;
    }

    .confirmCartItemsContainer>div>span {
        font-size: 1.5vmax;
    }

    .orderSummary {
        padding: 4vmax;
    }

    .orderSummary>div>div {
        font-size: 1.8vmax;
    }

    .orderSummaryTotal {
        font-size: 2vmax;
    }

    .orderSummary>button {
        font-size: 1.8vmax;
    }

}

@media (max-width: 426px) {
    .MuiTypography-body1 {
        font-size: 10px;
    }

    .MuiSvgIcon-root {
        font-size: 1.2rem !important;
    }

    .confirmOrderPage {
        grid-template-columns: 1fr;
        padding-bottom: 3vmax;
        height: 100%;
    }

    .confirmOrderPage>div:last-child {
        border-left: 0;
    }

    .confirmShippingArea>p {
        font-size: 3.5vmax;
    }

    .confirmShippingAreaBox>div>p {
        font-size: 2.8vmax;
    }

    .confirmShippingAreaBox>div>span {
        font-size: 2.8vmax;
    }

    .confirmCartItems {
        padding-bottom: 0px;
    }

    .confirmCartItems>p {
        font-size: 3vmax;
    }

    .confirmCartItemsContainer>div>img {
        width: 5vmax;
    }

    .confirmCartItemsContainer>div>a {
        font-size: 2.5vmax;
    }

    .confirmCartItemsContainer>div>span {
        font-size: 2.5vmax;
    }

    .orderSummary {
        padding: 4vmax;
    }

    .orderSummary>p {
        font-size: 3.8vmax;
    }

    .orderSummary>div>div {
        font-size: 2.8vmax;
    }

    .orderSummaryTotal {
        font-size: 3vmax;
    }

    .orderSummary>button {
        font-size: 2.8vmax;
    }



}