.sidebar {
    background-color: white;
    display: flex;
    flex-direction: column;
    padding: 3rem;
}

.sidebar>a:first-child {
    padding: 0;
}

.sidebar>a>img {
    width: 100%;
    transition: all 0.5s;
}

.sidebar>a>img:hover {
    filter: drop-shadow(0 0 10px tomato);
}

.sidebar a {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.493);
    font: 200 1rem "Roboto";
    padding: 1.5rem;
    transition: all 0.5s;
}

.sidebar a:hover {
    color: tomato;
    transform: scale(1.07);
}

.sidebar a>p {
    display: flex;
    align-items: center;
    margin: 0;
}

.sidebar a>p>svg {
    margin-right: 0.5rem;
}

.MuiTypography-root {
    background-color: white !important;
}

@media (max-width: 769px) {
    .sidebar {
        padding: 5px;
    }

    .sidebar a {
        padding: 1rem;
    }

    .sidebar>a>img {
        width: 30%;
    }
}

@media (max-width: 426px) {

    .sidebar a {
        padding: 0.5rem;
    }

    .sidebar>a>img {
        width: 35%;
    }

}