.updateProductContainer {
    width: 100%;
    box-sizing: border-box;
    border-left: 1px solid rgba(0, 0, 0, 0.13);
    display: flex;
    flex-direction: column;
    background-color: rgb(221, 221, 221);
    height: 100%;
    padding: 2vmax;
}

.updateProductContainer>h1 {
    color: rgba(0, 0, 0, 0.733);
    font: 300 2rem "Roboto";
    text-align: center;
}

.updateProductForm {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    margin: auto;
    padding: 3vmax;
    height: 100%;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.267);
}

.updateProductForm>div {
    display: flex;
    width: 100%;
    align-items: center;
}

.updateProductForm>div>input,
.updateProductForm>div>select,
.updateProductForm>div>textarea {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 1vmax cursive;
    outline: none;
    margin: 5px 0;
}

.updateproduct-form-icon {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}

#updateProductFormFile>input {
    display: flex;
    padding: 0%;
    margin-left: 3px;
}

#updateProductFormFile>input::file-selector-button {
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0%;
    font: 400 0.8vmax cursive;
    transition: all 0.5s;
    padding: 0 1vamx;
    color: rgba(0, 0, 0, 0.623);
    background-color: rgb(255, 255, 255);
}

#updateProductFormFile>input::file-selector-button:hover {
    background-color: rgb(235, 235, 235);
}

#updateProductFormImage {
    width: 100%;
    overflow: auto;
}

#updateProductFormImage>img {
    width: 2.5vmax;
    margin: 0 0.5vmax;
}

#updateProductBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1.2vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
    margin: 5px 0;
}

#updateProductBtn:hover {
    background-color: rgb(179, 66, 46);
}



@media (max-width: 769px) {
    .updateProductForm {
        height: 93vh;
        margin: 0 10vmax;
    }

    #updateProductFormImage>img {
        width: 5vmax;
    }

    .updateProductContainer {
        background-color: white;
    }

    .updateProductForm>div>input,
    .updateProductForm>div>select,
    .updateProductForm>div>textarea {
        height: 5vmax;
        font-size: 2vmax;
    }

    #updateProductBtn {
        font-size: 2.2vmax;
        padding: 1vmax;
    }

    #updateProductFormFile>img {
        width: 4vmax;
    }

    #updateProductFormFile>input::file-selector-button {
        font-size: 2vmax;
    }
}

@media (max-width: 426px) {

    .updateProductForm {
        margin: 7vmax;
        height: 80vh;
    }
    #updateProductFormImage>img {
        width: 6vmax;
    }


    .updateProductForm>div>input,
    .updateProductForm>div>select,
    .updateProductForm>div>textarea {
        height: 7vmax;
        font-size: 2.5vmax;
        padding: 3vmax 7vmax;
    }

    #updateProductBtn {
        font-size: 3.2vmax;
        padding: 2vmax;
    }

    #updateProductFormFile>img {
        width: 6vmax;
    }

    #updateProductFormFile>input::file-selector-button {
        font-size: 3vmax;
        margin-top: 4px;
    }
}