.productReviewContainer {
    width: 100%;
    box-sizing: border-box;
    border-left: 1px solid rgba(0, 0, 0, 0.158);
    height: 100vh;
}

.productReviewForm {
    width: 20rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 3vmax;
    background-color: white;
}

.productReviewForm>div {
    display: flex;
    width: 100%;
    align-items: center;
    margin: 2rem;
}

.productReviewForm>div>input {
    padding: 1vmax 4vmax;
    padding-right: 2vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 1vmax cursive;
    outline: none;
}

.productReview-form-icon {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}

.productReviewFormHeading {
    color: rgba(0, 0, 0, 0.733);
    font: 300 1.5rem "Roboto";
    margin: auto;
    width: 50%;
    padding: 1.5rem;
    text-align: center;
}

.productReviewTable {
    background-color: white;
    border: none !important;
}

.productReviewTable div {
    font: 300 1vmax "Roboto";
    color: rgba(0, 0, 0, 0.678);
    border: none !important;
}

.MuiDataGrid-columnHeader div {
    color: rgb(255, 255, 255);
}

#productReviewBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1.2vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

#productReviewBtn:hover {
    background-color: rgb(179, 66, 46);
}

.productReviewTable button {
    transition: all 0.5s;
}

.productReviewTable button:hover {
    color: rgb(236, 30, 30);
}


@media (max-width: 769px) {
    .productReviewContainer {
        border-top: 1px solid rgba(0, 0, 0, 0.158);
    }

    #productReviewBtn {
        padding: 1vmax;
        font-size: 1.8vmax;
    }

    .productReviewForm>div>input {
        font-size: 1.8vmax;
        padding: 1vmax 5vmax;
    }
    .productReviewTable div {
        font-size: 2vmax;
    }
}

@media (max-width: 426px) {
    .productReviewContainer {
        border-top: 1px solid rgba(0, 0, 0, 0.158);
    }

    #productReviewBtn {
        padding: 1vmax;
        font-size: 1.8vmax;
    }

    .productReviewForm>div>input {
        font-size: 2.8vmax;
        padding: 1vmax 6vmax;
    }

    .productReviewFormHeading {
        padding: 0;
    }

    #productReviewBtn {
        font-size: 2.8vmax;
    }
    .productReviewTable div {
        font-size: 3vmax;
    }
}