.productCard {
    width: 203px;
    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: rgb(48, 48, 48);
    margin: 2vmax;
    transition: all 0.5s;
    padding: 10px;
}

.productCard>img {
    width: 100%;
}

.productCard>p {
    margin-bottom: 0;
}

.productCard>div {
    display: flex;
    align-items: center;
}

.productCardSpan {
    font: 200 1vmax cursive;
    color: rgba(0, 0, 0, 0.699);
}

.productCard>span {
    font-size: 18px;
    color: tomato;
    font-weight: 500;
    margin-top: 5px;
}

.productCard:hover {
    box-shadow: 0 0 5px rgba(15, 15, 15, 0.26);
    transform: translateY(-1vmax);
}

@media (max-width: 769px) {
    .productCard {
        width: 178px;
    }

    .productCard>div {
        flex-direction: column;
        align-items: start;
    }
}

@media (max-width:426px) {
    .productCard {
        width: 60%;
    }

}