.loading{
    width: 100vw;
    height: 100vh;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    place-items: c;
    max-width: 100%;
}
.loading > div{
    width: 10vmax;
    height: 10vmax;
    border-bottom: 5px solid rgb(79, 79, 79);
    border-radius: 50%;
    animation: loadingRotate 800ms linear infinite;
}
@keyframes loadingRotate {
    to{
        transform: rotate(-360deg);
    }
}
