.speedDial {
    position: fixed;
    top: 6.7vmax;
    right: 0;
}

.speedDialIcon {
    width: 36px;
    height: 36px;
    border-radius: 100%;
}

.MuiSpeedDial-fab {
    width: 30px !important;
    height: 30px !important;
}

@media (max-width: 769px) {
    .speedDial {
        top: 10vmax;
        right: 0;
    }
}

@media (max-width: 426px) {
    .speedDial {
        top: 14vmax;
        right: 0;
    }
}