.UpdatePasswordContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(231, 231, 231);
    position: fixed;
    top: 0%;
    left: 0;
}

.UpdatePasswordBox {
    background-color: white;
    width: 25vw;
    height: 70vh;
    box-sizing: border-box;
    overflow: hidden;
}

.UpdatePasswordHeading {
    text-align: center;
    color: rgba(0, 0, 0, 0.664);
    font: 400 1.3vmax "Roboto";
    padding: 1.3vmax;
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    width: 50%;
    margin: auto;
}

.UpdatePasswordForm {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    margin: auto;
    padding: 2vmax;
    height: 70%;
    transition: all 0.5s;

}

.UpdatePasswordForm>div {
    display: flex;
    width: 100%;
    align-items: center;
}

.UpdatePasswordForm>div>input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 1vmax cursive;
    outline: none;
}

.UpdatePassword-form-icon {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}


.UpdatePasswordBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1.2vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.UpdatePasswordBtn:hover {
    background-color: rgb(179, 66, 46);
}

@media (max-width: 769px) {
    .UpdatePasswordBox {
        width: 50vw;
        height: 70vh;
    }

    .UpdatePasswordForm>div>input {
        height: 5vmax;
        font-size: 2vmax;
    }

    .login_UpdatePassword_toggle>p {
        height: 5vmax;
        font-size: 2.5vmax;
    }

    .UpdatePasswordBtn {
        font-size: 2.2vmax;
        padding: 1vmax;
    }

    .UpdatePasswordHeading {
        font-size: 2.3vmax;
    }
}

@media (max-width: 426px) {
    .UpdatePasswordContainer {
        background-color: white
    }

    .UpdatePasswordBox {
        width: 90%;
        height: 90%;
    }

    .UpdatePasswordForm>div>input {
        height: 7vmax;
        font-size: 2.5vmax;
        padding: 3vmax 7vmax;
    }

    .UpdatePasswordBox>div>button {
        margin-top: 6vmax;
    }

    .UpdatePasswordBtn {
        font-size: 3.2vmax;
        padding: 2vmax;
    }

    .UpdatePasswordHeading {
        font-size: 3.3vmax;
    }
}