.LoginSignupContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(231, 231, 231);
    position: fixed;
    top: 0%;
    left: 0;
}

.LoginSignupBox {
    background-color: white;
    width: 25vw;
    height: 70vh;
    box-sizing: border-box;
    overflow: hidden;
}

.login_signup_toggle {
    display: flex;
    height: 3vmax;
}

.login_signup_toggle>p {
    color: rgba(0, 0, 0, 0.678);
    font: 300 1.3vmax "Roboto";
    transition: all 0.5s;
    cursor: pointer;
    display: grid;
    place-items: center;
    width: 100%;
}

.login_signup_toggle>p:hover {
    color: tomato;
}

.LoginSignupBox>div>button {
    background-color: tomato;
    height: 3px;
    width: 50%;
    border: none;
    transition: all 0.5s;
}

.loginForm,
.signupForm {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    margin: auto;
    padding: 2vmax;
    height: 70%;
    transition: all 0.5s;

}

.signupForm {
    transform: translateX(-100vmax) translateY(-100%);
}

.loginForm>div,
.signupForm>div {
    display: flex;
    width: 100%;
    align-items: center;
}

.loginForm>div>input,
.signupForm>div>input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 1vmax cursive;
    outline: none;
}

.login-form-icon,
.signup-form-icon {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}

.loginForm>a {
    color: rgba(0, 0, 0, 0.651);
    text-decoration: none;
    align-self: flex-end;
    transition: all 0.5s;
    font: 500 1vmax "Gill Sans";
}

.loginForm>a:hover {
    color: black;
}

#registerImage>img {
    width: 2.5vmax;
    border-radius: 100%;
    margin: 1vmax;
}

#registerImage>input {
    display: flex;
    padding: 0%;
    margin-left: 3px;
}

#registerImage>input::file-selector-button {
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0%;
    font: 400 0.8vmax cursive;
    transition: all 0.5s;
    padding: 0 1vamx;
    color: rgba(0, 0, 0, 0.623);
    background-color: rgb(255, 255, 255);
}

#registerImage>input::file-selector-button:hover {
    background-color: rgb(235, 235, 235);
}

.loginBtn,
.signupBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1.2vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.loginBtn:hover,
.signupBtn:hover {
    background-color: rgb(179, 66, 46);
}

.shiftToLeft {
    transform: translateX(-100%);
}

.shiftToNeutral {
    transform: translateX(0%);
}

.shiftToNeutralForm {
    transform: translateX(0%) translateY(-100%);
}

.shiftToRight {
    transform: translateX(100%);
}

@media (max-width: 769px) {
    .LoginSignupBox {
        width: 50vw;
        height: 70vh;
    }

    .loginForm>div>input,
    .signupForm>div>input {
        height: 5vmax;
        font-size: 2vmax;
    }

    .login_signup_toggle>p {
        height: 5vmax;
        font-size: 2.5vmax;
    }

    .LoginSignupBox>div>button {
        margin-top: 5vmax;
    }

    .loginForm>a {
        font-size: 2vmax;
    }

    .loginBtn,
    .signupBtn {
        font-size: 2.2vmax;
        padding: 1vmax;
    }

    #registerImage>img {
        width: 4vmax;
    }

    #registerImage>input::file-selector-button {
        font-size: 2vmax;
    }
}

@media (max-width: 426px) {
    .LoginSignupContainer {
        background-color: white
    }

    .LoginSignupBox {
        width: 90%;
        height: 90%;
    }

    .loginForm>div>input,
    .signupForm>div>input {
        height: 7vmax;
        font-size: 2.5vmax;
        padding: 3vmax 7vmax;
    }

    .login_signup_toggle>p {
        font-size: 3vmax;
    }

    .LoginSignupBox>div>button {
        margin-top: 6vmax;
    }

    .loginForm>a {
        font-size: 3vmax;
    }
    .loginBtn, .signupBtn {
        font-size: 3.2vmax;
        padding: 2vmax;
    }
    #registerImage>img {
        width: 6vmax;
    }
    #registerImage>input::file-selector-button {
        font-size: 3vmax;
        margin-top: 4px;
    }
}