.shippingContainer {
    width: 100vw;
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.shippingBox {
    background-color: white;
    width: 25vw;
    height: 90vh;
    box-sizing: border-box;
    overflow: hidden;
}
.shippingHeading {
    text-align: center;
    color: rgba(0, 0, 0, 0.664);
    font: 400 1.3vmax "Roboto";
    padding: 1.3vmax;
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    width: 50%;
    margin: auto;
}
.shippingForm {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    margin: auto;
    padding: 2vmax;
    height: 80%;
    transition: all 0.5s;
}
.shippingForm>div {
    display: flex;
    width: 100%;
    align-items: center;
}
.shippingForm>div>input,
.shippingForm>div>select {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 1vmax cursive;
    outline: none;
}

.shippingFormIcon {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}
.shippingBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1.2vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.shippingBtn:hover {
    background-color: rgb(179, 66, 46);
}


@media (max-width: 769px) {
    .shippingForm{
        height: 90%;
    }
    .shippingBox {
        width: 50vw;
        height: 70vh;
    }

    .shippingForm>div>input {
        height: 5vmax;
        font-size: 2vmax;
    }

    .shippingBtn {
        font-size: 2.2vmax;
        padding: 1vmax;
    }

    .shippingHeading {
        font-size: 2.3vmax;
    }
}

@media (max-width: 426px) {
    .shippingContainer {
        background-color: white
    }

    .resetPasswordBox {
        width: 90%;
        height: 90%;
    }

    .shippingForm>div>input {
        height: 7vmax;
        font-size: 2.5vmax;
        padding: 3vmax 7vmax;
    }

    .shippingBtn {
        font-size: 3.2vmax;
        padding: 2vmax;
    }

    .shippingHeading {
        font-size: 3.3vmax;
    }
}