.orderDetailsPage {
    background-color: white;
}

.orderDetailsContainer>h1 {
    font: 300 3vmax "Roboto";
    margin: 4vmax 0;
    color: tomato;
}

.orderDetailsContainer {
    padding: 5vmax;
    padding-bottom: 0%;
}

.orderDetailsContainer>p {
    font: 400 1.8vmax "Roboto";
}

.orderDetailsContainerBox,
.orderDetailsCartItemsContainer {
    margin: 2vmax;
}

.orderDetailsContainerBox>div {
    display: flex;
    align-items: center;
}

.orderDetailsContainerBox>div>p {
    font: 400 1vmax "Roboto";
    color: black;
}

.orderDetailsContainerBox>div>span {
    color: #575757;
    font: 100 1vmax "Roboto";
    margin: 0 1vmax;
}

.orderDetailsCartItems>p {
    font: 400 1.8vmax "Roboto";
}

.orderDetailsCartItems {
    padding: 5vmax;
    padding-top: 2vmax;
    border-top: 1px solid rgba(0, 0, 0, 0.164);
}

.orderDetailsCartItemsContainer>div {
    display: flex;
    font: 400 1vmax "Roboto";
    align-items: center;
    margin: 2vmax 0;
}

.orderDetailsCartItemsContainer>div>img {
    width: 3vmax;
}

.orderDetailsCartItemsContainer>div>a {
    width: 60%;
    text-decoration: none;
    color: #575757;
    margin: 0 2vmax;
}

.orderDetailsCartItemsContainer>div>span {
    font: 100 1vmax "Roboto";
    color: #5e5e5e;
}

@media (max-width: 769px) {
    .orderDetailsContainer>h1 {
        font-size: 4vmax;
    }

    .orderDetailsContainer>p {
        font-size: 2.8vmax;
    }

    .orderDetailsContainerBox>div>p {
        font-size: 2vmax;
    }

    .orderDetailsContainerBox>div>span {
        font-size: 2vmax;
    }

    .orderDetailsCartItemsContainer>div>img {
        width: 7vmax;
    }

    .orderDetailsCartItemsContainer>div>a {
        font-size: 2.4vmax;
    }

    .orderDetailsCartItemsContainer>div>span {
        font-size: 2.4vmax;
    }
}

@media (max-width: 426px) {
    .orderDetailsContainer>h1 {
        font-size: 4.5vmax;
    }

    .orderDetailsContainer>p {
        font-size: 3.5vmax;
    }

    .orderDetailsContainerBox>div>p {
        font-size: 2.5vmax;
    }

    .orderDetailsContainerBox>div>span {
        font-size: 2.5vmax;
    }

    .orderDetailsCartItemsContainer>div>img {
        width: 6vmax;
    }

    .orderDetailsCartItemsContainer>div>a {
        font-size: 2.5vmax;
    }

    .orderDetailsCartItemsContainer>div>span {
        font-size: 2.3vmax;
    }
}