.paymentContainer {
    display: grid;
    place-items: center;
    background-color: rgb(255, 255, 255);
    height: 65vh;
    margin: 2vmax;
}

.paymentForm {
    width: 22%;
    height: 100%;
}

.paymentForm>p {
    font: 400 2vmax "Roboto";
    color: rgba(0, 0, 0, 0.753);
    border-bottom: 1px solid rgba(0, 0, 0, 0.13);
    margin: auto;
    width: 50%;
    text-align: center;
}

.paymentForm>div {
    display: flex;
    align-items: center;
    margin: 2vmax 0;
}

.paymentInput {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 1vmax cursive;
    outline: none;
}

.paymentFormIcon {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}

.paymentFormBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1.2vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.paymentFormBtn:hover {
    background-color: rgb(179, 66, 46);
}


@media (max-width: 769px) {
    .paymentForm {
        width: 40%;
    }

    .paymentInput {
        height: 5vmax;
        font-size: 2vmax;
    }

    .paymentFormBtn {
        font-size: 2.2vmax;
        padding: 1vmax;
    }

    .paymentForm>p {
        font-size: 3vmax;
    }
}

@media (max-width: 426px) {
    .paymentForm {
        width: 90%;
    }

    .paymentForm>p {
        font-size: 5vmax;
    }

    .paymentInput {
        height: 7vmax;
        font-size: 2.5vmax;
        padding: 3vmax 7vmax;
    }

    .paymentFormBtn {
        font-size: 3.2vmax;
        padding: 2vmax;
    }

}