.mainContactDiv {
    width: 100%;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}
.mainContactDiv >div{
    width: 80%;
    height: 40vmax;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.5s;
}

.contactIcon {
    font: 600 3vmax "roboto" !important;
}

.mainContactDiv >div >a{ 
    text-decoration: none;
    font: 600 3vmax "roboto";
    color: rgb(71 70 70 / 65%);;
    margin-left: 1vmax;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all 0.5s;
    padding: 10px;
}
.mainContactDiv >div >a:hover{
    color: tomato;
    background-color: rgba(175, 174, 174, 0.254);
} 