.orderSuccess {
    margin: auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 50vh;
}

.orderSuccess>svg {
    font-size: 5vmax;
    color: tomato;
}

.orderSuccess>p {
    font-size: 2vmax;
}

.orderSuccess>a {
    margin-top: 1vmax;
    background-color: rgb(51, 51, 51);
    color: white;
    border: none;
    padding: 1vmax 3vmax;
    cursor: pointer;
    font: 400 1vmax "Roboto";
    text-decoration: none;
}


@media (max-width: 769px) {

    .orderSuccess>svg {
        font-size: 5vmax;
    }

    .orderSuccess>a {
        padding: 1.5vmax 3.5vmax;
        font-size: 1.5vmax;
    }

    .orderSuccess>p {
        font-size: 2vmax;
    }
}

@media (max-width: 426px) {

    .orderSuccess>svg {
        font-size: 6vmax;
    }

    .orderSuccess>a {
        padding: 2vmax 4vmax;
        font-size: 2vmax;
    }

    .orderSuccess>p {
        font-size: 3vmax;
    }
}