.main-product {
    width: 100%;
    display: flex;
}

.products-div {
    width: 100%;
}

.productHeading {
    margin: 2vmax auto;
    width: 15vw;
    border-bottom: 1px solid rgba(0, 0, 0, 0.171);
    padding: 2vmax;
    color: rgba(0, 0, 0, 0.678);
    font: 500 1.5vmax "Roboto";
    text-align: center;
}

.products {
    display: flex;
    flex-wrap: wrap;
    padding: 0 5vmax;
    justify-content: center;
    min-height: 30vh;
}

.paginationBox {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 6vmax;
}

.pagination {
    display: flex;
    justify-content: center;
    padding: 0;
}

.page-item {
    background-color: white;
    list-style: none;
    border: 1px solid rgba(0, 0, 0, 0.178);
    padding: 1vmax 1.5vmax;
    transition: all 0.3s;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.page-item:first-child {
    border-radius: 5px 0 0 5px;
}

.page-item:last-child {
    border-radius: 0 5px 5px 0;
}

.page-link {
    text-decoration: none;
    font: 300 1vmax "Roboto";
    color: rgb(80, 80, 80);
    transition: all 0.3s;
}

.page-item:hover {
    background-color: rgb(230 230 230);
}

.page-item:hover .page-link {
    color: rgb(80, 80, 80);
}

.pageItemActive {
    background-color: tomato;
}

.pageLinkActive {
    color: white;
}

.filterBox {
    width: 15%;
    padding: 2vmax;
 
    display: flex;
    flex-direction: column;
}

.filterBox>fieldset {
    border: 1px solid rgba(0, 0, 0, 0.329);
}

.categoryBox {
    padding: 0;
}

.category-link {
    list-style: none;
    color: rgba(0, 0, 0, 0.61);
    font: 400 1.1vmax "Roboto";
    transition: all 0.5s;
    cursor: pointer;
    margin: 0.4vmax;
}

.category-link:hover {
    color: tomato;
}

@media (max-width: 769px) {
    .productHeading {
        font-size: 2vmax;
    }

    .page-link {
        font-size: 2vmax;
    }

    .filterBox {
        width: 18%;
    }

    .page-link {
        font-size: 1.5vmax;
    }

    .category-link {
        font-size: 1.5vmax;
    }

}

@media (max-width: 426px) {
    .main-product {
        flex-direction: column;
        display: flex;
    }

    .filterBox {
        width: 50%;
        margin: auto;
        order: 2;
    }
    .products-div{
        order: 1;
    }

    .page-link {
        font-size: 3vmax;
    }

    .category-link {
        font-size: 3vmax;
    }
}