.main-header {
    width: 100%;
    height: auto;
}

.header-div {
    background-color: rgb(64 64 64 / 82%);
    padding: 10px 12px;
}

.header {
    width: 100%;
    color: white;
    display: flex;
    align-items: center;
}

.header-img-div {
    width: 15%;
}

.header-img-div img {
    width: 140px;
    height: 50px;
    transition: all 0.5s;
}

.header-img-div img:hover {
    filter: drop-shadow(0 0 10px tomato);
}

.header-links {
    width: 58%;
    display: block;
    text-transform: uppercase;
}

.header-links a {
    margin-left: 50px;
    text-decoration: none;
    color: white;
    transition: all 0.5s;
}

.header-links a:hover {
    color: tomato;
}

.header-icons-div a {
    margin-left: 10px;
    text-decoration: none;
    color: white;
    transition: all 0.5s;
}

.header-icons-div a:hover {
    color: tomato;
}

.notification {
    color: white;
    text-decoration: none;
    position: relative;
    display: inline-block;
    border-radius: 2px;
}

.notification .badge {
    position: absolute;
    font-size: 10px;
    top: -10px;
    right: -10px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1px 4px;
    border-radius: 100%;
    background-color: tomato;
    color: white;
}


.header-search-div input {
    width: 100%;
    border: none;
    margin-left: 5px;
}


.header-search-div input:focus-visible {
    outline: none;
}

.header-search-div {
    height: 35px;
    border-radius: 5px;
    background-color: white;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .2), 0 2px 2px 0 rgba(0, 0, 0, .14), 0 1px 5px 0 rgba(0, 0, 0, .12);
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0 auto;
    padding: 0 10px;
}

.header-search-div button {
    background: none;
    border: none;
    cursor: pointer;
}

.header-search-icon:hover {
    color: tomato;
    transform: scale(1.5);
}

.header-search-icon {
    color: rgb(64 64 64 / 82%);
    transition: all 0.5s;
   
}

.mobile_nav {
    color: white;
    margin-left: 10px;
    font-size: 20px;
    display: none;
}

.mobile_nav_link {
    z-index: 555;
    background-color: rgb(64 64 64 / 82%);
    width: 60%;
    padding-top: 50px;
    padding-bottom: 50px;
    position: absolute;
    top: 50px;
    left: 0;
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, .2), 0 8px 10px 1px rgba(0, 0, 0, .14), 0 3px 14px 2px rgba(0, 0, 0, .12);
}

.mobile-nov-modal a {
    padding-top: 10px;
    padding-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    color: white;
    text-decoration: none;
    transition: all 0.5s;
}

.mobile-nov-modal a:hover {
    color: tomato;
}

@media (max-width: 769px) {
    .header-img-div {
        width: 20%;
    }

    .header-links {
        width: 48%;
    }

    .header-links a {
        margin-left: 20px;
    }

    .header-search-div {
        width: 22%;
    }
}

@media (max-width: 426px) {
    .header-img-div img {
        width: 60px;
        height: 35px;
    }

    .header-img-div {
        width: 20%;
    }

    .header-links {
        width: 48%;
    }

    .header-links a {
        margin-left: 10px;
        font-size: 12px;
    }

    .header-search-div {
        width: 55%;
    }

    .mobile_nav {
        display: block;
    }

    .header-links {
        display: none;
    }
}

@media (max-width: 376px) {
    .header-search-div {
        height: 30px;
        width: 51%;
    }
}

@media (max-width: 321px) {
    .header-img-div img {
        width: 51px;
        height: 30px;
    }

    .header-search-div {
        width: 45%;
        height: 30px;
    }

}