.footer {
    width: 100%;
    height: auto;
    padding: 10px 0;
    background-color: rgb(34, 33, 33);
    color: white;
    display: flex;
    flex-direction: row;
}

.leftFooter {
    width: 33%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.leftFooter>p {
    text-align: center;
    font-size: 1.2vmax;
}

.lfImgs>img {
    width: 10vmax;
    margin: 10px;
    cursor: pointer;
    display: flex;
    flex-direction: column;
}

.midFooter {
    width: 33%;
    height: auto;
    text-align: center;
}

.midFooter>h2{
    transition: all 0.5s;
}
.midFooter>h2:hover {
    filter: drop-shadow(0 0 10px tomato);
}

.midFooter>p {
    max-width: 60%;
    margin: 10px auto;

}

.rightFooter {
    width: 33%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.rightFooter>h2 {
    text-decoration: underline;
}

.rfLinks a {
    display: flex;
    flex-direction: column;
    text-decoration: none;
    font-size: 1.3vmax;
    color: white;
    margin: 0.5vmax;
    transition: all 0.5s;
    align-items: center;
}

.rfLinks>a:hover {
    color: #eb4034;
}

@media (max-width: 769px) {
    .leftFooter h2 {
        font-size: 23px;
        margin: 2px;
    }

    .leftFooter>p {
        font-size: 15px;
    }

    .lfImgs>img {
        width: 11vmax;
    }

    .midFooter h2 {
        margin: 5px;
        font-size: 23px;
    }

    .midFooter>p {
        font-size: 11px;
    }

    .rightFooter h2 {
        margin: 5px;
        font-size: 23px;
    }

    .rfLinks a {
        font-size: 15px;
    }
}

@media (max-width: 426px) {
    .footer {
        flex-direction: column;
    }

    .leftFooter {
        width: 100%;
    }

    .leftFooter>p {
        font-size: 12px;
        margin: 0;
    }

    .leftFooter h2 {
        margin: 5px;
    }

    .lfImgs>img {
        width: 20vmax;
    }

    .midFooter {
        width: 100%;
        order: 2;
        margin-top: 5px;
    }

    .midFooter h2 {
        margin: 5px;
    }

    .midFooter p {
        font-size: 12px;
    }

    .rightFooter {
        width: 100%;
        order: 1;
    }

    .rightFooter h2 {
        margin: 5px;
    }

    .rfLinks>a {
        text-decoration: none;
        font-size: 12px;
    }
}

