.updateProfileContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(231, 231, 231);
    position: fixed;
    top: 0%;
    left: 0;
}

.updateProfileBox {
    background-color: white;
    width: 25vw;
    height: 70vh;
    box-sizing: border-box;
    overflow: hidden;
}

.updateProfileHeading {
    text-align: center;
    color: rgba(0, 0, 0, 0.664);
    font: 400 1.3vmax "Roboto";
    padding: 1.3vmax;
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    width: 50%;
    margin: auto;
}

.updateProfileForm {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    margin: auto;
    padding: 2vmax;
    height: 70%;
    transition: all 0.5s;

}

.updateProfileForm>div {
    display: flex;
    width: 100%;
    align-items: center;
}

.updateProfileForm>div>input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 1vmax cursive;
    outline: none;
}

.updateProfile-form-icon {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}



#updateProfileImage>img {
    width: 2.5vmax;
    height: 2.5vmax;
    border-radius: 100%;
    margin: 1vmax;
}

#updateProfileImage>input {
    display: flex;
    padding: 0%;
    margin-left: 3px;
}

#updateProfileImage>input::file-selector-button {
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0%;
    font: 400 0.8vmax cursive;
    transition: all 0.5s;
    padding: 0 1vamx;
    color: rgba(0, 0, 0, 0.623);
    background-color: rgb(255, 255, 255);
}

#updateProfileImage>input::file-selector-button:hover {
    background-color: rgb(235, 235, 235);
}

.updateProfileBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1.2vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.updateProfileBtn:hover {
    background-color: rgb(179, 66, 46);
}

@media (max-width: 769px) {
    .updateProfileBox {
        width: 50vw;
        height: 70vh;
    }

    .updateProfileForm>div>input {
        height: 5vmax;
        font-size: 2vmax;
    }

    .login_updateProfile_toggle>p {
        height: 5vmax;
        font-size: 2.5vmax;
    }

    .updateProfileBtn {
        font-size: 2.2vmax;
        padding: 1vmax;
    }

    #updateProfileImage>img {
        width: 4vmax;
    }

    #updateProfileImage>input::file-selector-button {
        font-size: 2vmax;
    }

    .updateProfileHeading {
        font-size: 2.3vmax;
    }
}

@media (max-width: 426px) {
    .updateProfileContainer {
        background-color: white
    }

    .updateProfileBox {
        width: 90%;
        height: 90%;
    }

    .updateProfileForm>div>input {
        height: 7vmax;
        font-size: 2.5vmax;
        padding: 3vmax 7vmax;
    }

    .updateProfileBox>div>button {
        margin-top: 6vmax;
    }

    .updateProfileBtn {
        font-size: 3.2vmax;
        padding: 2vmax;
    }

    #updateProfileImage>img {
        width: 6vmax;
    }

    #updateProfileImage>input::file-selector-button {
        font-size: 3vmax;
        margin-top: 4px;
    }

    .updateProfileHeading {
        font-size: 3.3vmax;
    }
}