.newProductContainer {
    width: 100%;
    box-sizing: border-box;
    border-left: 1px solid rgba(0, 0, 0, 0.13);
    display: flex;
    flex-direction: column;
    background-color: rgb(221, 221, 221);
    height: 100%;
    padding: 2vmax;
}

.newProductContainer>h1 {
    color: rgba(0, 0, 0, 0.733);
    font: 300 2rem "Roboto";
    text-align: center;
}

.createProductForm {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    margin: auto;
    padding: 3vmax;
    height: 100%;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.267);
}

.createProductForm>div {
    display: flex;
    width: 100%;
    align-items: center;
}

.createProductForm>div>input,
.createProductForm>div>select,
.createProductForm>div>textarea {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 1vmax cursive;
    outline: none;
    margin: 5px 0;
}

.newproduct-form-icon {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}

#createProductFormFile>input {
    display: flex;
    padding: 0%;
    margin-left: 3px;

}

#createProductFormFile>input::file-selector-button {
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0%;
    font: 400 0.8vmax cursive;
    transition: all 0.5s;
    padding: 0 1vamx;
    color: rgba(0, 0, 0, 0.623);
    background-color: rgb(255, 255, 255);
}

#createProductFormFile>input::file-selector-button:hover {
    background-color: rgb(235, 235, 235);
}

#createProductFormImage {
    width: 100%;
    overflow: auto;
}

#createProductFormImage>img {
    width: 2.5vmax;
    margin: 0 0.5vmax;
}

#createProductBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1.2vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
    margin: 5px 0;
}

#createProductBtn:hover {
    background-color: rgb(179, 66, 46);
}



@media (max-width: 769px) {
    .createProductForm {
        margin: 0 10vmax;
    }

    #createProductFormImage>img {
        width: 5vmax;
    }

    .newProductContainer {
        background-color: white;
    }

    .createProductForm>div>input,
    .createProductForm>div>select,
    .createProductForm>div>textarea {
        height: 5vmax;
        font-size: 2vmax;
    }

    #createProductBtn {
        font-size: 2.2vmax;
        padding: 1vmax;
    }

    #createProductFormFile>img {
        width: 4vmax;
    }

    #createProductFormFile>input::file-selector-button {
        font-size: 2vmax;
    }
}

@media (max-width: 426px) {

    .createProductForm {
        margin: 7vmax;
    }

    #createProductFormImage>img {
        width: 6vmax;
    }

    .createProductForm>div>input,
    .createProductForm>div>select,
    .createProductForm>div>textarea {
        height: 7vmax;
        font-size: 2.5vmax;
        padding: 0vmax 7vmax;
    }

    #createProductBtn {
        font-size: 3.2vmax;
        padding: 2vmax;
    }

    #createProductFormFile>img {
        width: 6vmax;
    }

    #createProductFormFile>input::file-selector-button {
        font-size: 3vmax;
        margin-top: 4px;
    }
}