.updateOrderForm {
    margin: 5vmax 0;
    padding: 3vmax;
    background-color: white;
}

.updateOrderForm>div {
    display: flex;
    width: 100%;
    align-items: center;
}

.updateOrderForm>div>select {
    padding: 1vmax 4vmax;
    width: 100%;
    margin: 2rem 0;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 1vmax cursive;
    outline: none;
}

.newproduct-form-icon {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}


#updateOrderBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1.2vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

#updateOrderBtn:hover {
    background-color: rgb(179, 66, 46);
}



@media (max-width: 769px) {
    .updateOrderForm {
        height: 93vh;
        margin: 0 10vmax;
    }



    .updateOrderForm>div>select {
        height: 5vmax;
        font-size: 2vmax;
    }

    #updateOrderBtn {
        font-size: 2.2vmax;
        padding: 1vmax;
    }

    #updateOrderFormFile>input::file-selector-button {
        font-size: 2vmax;
    }
}

@media (max-width: 426px) {

    .updateOrderForm {
        margin: 7vmax;
        height: 80vh;
    }

    .updateOrderForm>div>select {
        height: 7vmax;
        font-size: 2.5vmax;
        padding: 0vmax 7vmax;
    }

    #updateOrderBtn {
        font-size: 3.2vmax;
        padding: 2vmax;
    }

    #updateOrderFormFile>input::file-selector-button {
        font-size: 3vmax;
        margin-top: 4px;
    }
}