.profileContainer {
    display: flex;
    width: 100vw;
    max-width: 100%;
    background-color: white;
}

.profileContainer>div {
    display: flex;
    height: 100vh;
    width: 100vw;
    max-width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.profileContainer>div:first-child>h1 {
    color: rgba(0, 0, 0, 0.555);
    font: 500 2.2vmax "Roboto";
    transform: translateX(-10vmax) translateY(-2vmax);
}

.profileContainer>div:first-child>img {
    width: 20vmax;
    height: 20vmax;
    border-radius: 100%;
    transition: all 0.5s;
}

.profileContainer>div:first-child>img:hover {
    transform: scale(1.05);
}

.profileContainer>div:first-child>a {
    border: none;
    background-color: tomato;
    font: 400 1vmax "Roboto";
    color: white;
    text-decoration: none;
    padding: 0.5vmax;
    width: 30%;
    margin: 4vmax;
    text-align: center;
    transition: all 0.5s;
}

.profileContainer>div:first-child>a:hover {
    background-color: rgb(204, 78, 56);
}

.profileContainer>div:last-child {
    justify-content: space-evenly;
    align-items: flex-start;
    padding: 5vmax;
    box-sizing: border-box;
}

.profileContainer>div:last-child>div>h4 {
    color: black;
    font: 400 1.2vmax "Roboto";
}

.profileContainer>div:last-child>div>p {
    color: black;
    font: 400 1vmax cursive;
    margin: 0.2vmax;
}

.profileContainer>div:last-child>div:last-child {
    display: flex;
    flex-direction: column;
    width: 60%;
}

.profileContainer>div:last-child>div:last-child>a {
    border: none;
    background-color: rgb(68, 68, 68);
    padding: 0.5vmax;
    text-align: center;
    font: 400 1vmax "Roboto";
    color: white;
    text-decoration: none;
    transition: all 0.5s;
    margin: 1vmax 0;
}

.profileContainer>div:last-child>div:last-child>a:hover {
    background-color: rgb(31, 31, 31);
}


@media (max-width: 769px) {
    .profileContainer>div:first-child>h1 {
        font-size: 3vmax;
    }

    .profileContainer>div:first-child>img {
        width: 30vmax;
    }

    .profileContainer>div:first-child>a {
        font-size: 1.8vmax;
        padding: 1vmax 2vmax;
    }

    .profileContainer>div:last-child>div>h4 {
        font-size: 2.5vmax;
    }

    .profileContainer>div:last-child>div>p {
        font-size: 2vmax;
    }

    .profileContainer>div:last-child>div:last-child>a {
        padding: 1vmax 2vmax;
        font-size: 1.8vmax;
    }
}

@media (max-width: 426px) {
    .profileContainer {
        flex-direction: column;
    }

    .profileContainer>div {
        height: 60vh;
    }

    .profileContainer>div:first-child>h1 {
        font-size: 4vmax;
    }

    .profileContainer>div:first-child>img {
        width: 35vmax;
    }

    .profileContainer>div:first-child>a {
        font-size: 2.5vmax;
        padding: 1.5vmax 2.5vmax;
    }

    .profileContainer>div:last-child {
        text-align: center;
        align-items: center;
    }
    .profileContainer>div:last-child>div>h4 {
        font-size: 3vmax;
    }
    .profileContainer>div:last-child>div>p {
        font-size: 2.5vmax;
    }
    .profileContainer>div:last-child>div:last-child>a {
        font-size: 2.5vmax;
        padding: 1.5vmax 2.5vmax;
    }
}