.myOrderPage {
    padding: 2vmax 3.5vmax;
    width: 100vw;
    max-width: 100%;
    box-sizing: border-box;
    background-color: rgb(235, 235, 235);
    height: 100vh;
    display: flex;
    flex-direction: column;
}

#myOrdersHeading {
    text-align: center;
    font: 400 1.3vmax "Roboto";
    padding: 0.5vmax;
    box-sizing: border-box;
    color: rgb(255, 255, 255);
    transition: all 0.5s;
    background-color: rgb(41, 41, 41) !important;
}

.myOrderTable {
    background-color: white;
}

.myOrderTable div {
    font: 300 1.1vmax "Roboto";
    color: rgba(0, 0, 0, 0.678);
    border: none;
}

.myOrderTable a {
    color: rgba(0, 0, 0, 0.527);
    transition: all 0.5s;
}

.myOrderTable a:hover {
    color: tomato;
}

.MuiDataGrid-columnHeader {
    background-color: tomato;
    padding: 1vmax !important;
}

.MuiDataGrid-columnHeader div {
    color: rgb(255, 255, 255);
    font: 500 1.2vmax "Roboto" !important;
}

.MuiDataGrid-iconSeparator {
    display: none !important;
}

@media (max-width: 769px) {
    .myOrderPage {
        padding: 0;
    }

    #myOrdersHeading {
        font-size: 2.1vmax;
        padding: 3vmax;
    }

    .myOrderTable div {
        font-size: 1.9vmax;
    }

    .MuiDataGrid-columnHeader div {
        font-size: 1.7vmax !important;
    }
}

@media (max-width: 426px) {
    .myOrderPage {
        padding: 0;
    }

    #myOrdersHeading {
        font-size: 3.1vmax;
        padding: 3vmax;
    }

    .myOrderTable div {
        font-size: 2.5vmax;
    }

    .MuiDataGrid-columnHeader {
        padding: 4vmax 0 !important;
    }

    .MuiDataGrid-columnHeader div {
        font-size: 2vmax !important;
    }
}