.productDetails {
    display: flex;
    background-color: white;
    max-width: 100%;
    padding: 6vmax;
    align-items: center;
    margin: 0 auto;
}

.productDetails-1 {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 2vmax;
    box-sizing: border-box;
}

.productDetails-2 {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 2vmax;
    box-sizing: border-box;
    justify-content: center;
    align-items: baseline;
}

.CarouselImage {
    width: 400px;
}

.detailBlock-1>h2 {
    color: rgb(54, 54, 54);
    font: 600 1.6vmax "Roboto";
    margin: 0;
}

.detailBlock-1>p {
    color: rgb(54, 54, 54, 0.582);
    font: 200 1vmax "cursive";
}

.detailBlock-2 {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-top: 1px solid rgba(0, 0, 0, 0.205);
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    width: 70%;
    padding: 1vmax 0;
}
.detailBlock-2-span{
    font: 200 1vmax cursive;
    color: rgba(0, 0, 0, 0.699);
}


.detailBlock-3 {
    width: 70%;
}

.detailBlock-3>h1 {
    color: rgba(17, 17, 17, 0.795);
    font: 400 1.8vmax "Frankin Gothic Medium";
    margin: 1vmax 0;
}

.detailBlock-3-1 {
    display: flex;
    align-items: center;
}

.detailBlock-3-1-1>button {
    border: none;
    background-color: rgba(0, 0, 0, 0.616);
    padding: 0.5vmax;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
}

.detailBlock-3-1-1>button:hover {
    background-color: rgba(0, 0, 0, 0.767);
}

.detailBlock-3-1-1>input {
    border: none;
    padding: 0.5vmax;
    width: 2vmax;
    text-align: center;
    outline: none;
    font: 400 1.2vmax "Roboto";
    color: rgba(0, 0, 0, 0.74);
}

.detailBlock-3-1>button:last-child {
    border: none;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
    background-color: tomato;
    font: 500 0.7vmax "Roboto";
    border-radius: 20px;
    padding: 0.5vmax 2vmax;
    margin: 1vmax;
    outline: none;
}

.detailBlock-3-1>button:last-child:hover {
    background-color: rgb(214, 84, 61);
}

.detailBlock-3>p {
    border-top: 1px solid rgba(0, 0, 0, 0.205);
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    padding: 1vmax 0;
    color: rgba(0, 0, 0, 0.651);
    font: 400 1vmax "Roboto";
    margin: 1vmax 0;
}

.detailBlock-4 {
    color: rgba(0, 0, 0, 0.897);
    font: 500 1.2vmax "sans-serif";
}

.detailBlock-4>p {
    color: rgba(0, 0, 0, 0.897);
    font: 300 0.8vmax "sans-serif";
}

.submitReview {
    border: none;
    background-color: tomato;
    font: 500 0.8vmax "Roboto";
    border-radius: 20px;
    padding: 0.6vmax 2vmax;
    margin: 1vmax;
    color: white;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
    width: 30%;
}

.submitReview:hover {
    background-color: rgb(197, 68, 45);
    transform: scale(1.1);
}

.submitDialog{
    display: flex;
    flex-direction: column;
}
.submitReviewTextArea{
    border: 1px solid rgba(0, 0, 0, 0.082);
    margin: 1vmax 0;
    outline: none;
    padding: 1rem;
    font: 300 1rem "Roboto";
}

.reiews{
    display: flex;
    overflow: auto;
}
.reviewsHeading {
    color: #000000be;
    font: 500 1.4vmax "Roboto";
    text-align: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.226);
    padding: 1vmax;
    width: 20vmax;
    margin: auto;
    margin-bottom: 4vmax ;
}
.noReviews{
    color: rgba(0, 0, 0, 0.548);
    text-align: center;
    font: 400 1.3vmax "Gill Sans";
}

@media (max-width: 769px) {
    .CarouselImage {
        width: 300px;
    }

    .detailBlock-1>h2 {
        font-size: 2.4vmax;
    }

    .detailBlock-1>p {
        font-size: 1.5vmax;
    }

    .detailBlock-2>span {
        font-size: 1.5vmax;
    }

    .detailBlock-1>h2 {
        font-size: 4vmax;
    }

    .detailBlock-3>h1 {
        font-size: 3.5vmax;
    }

    .detailBlock-3>h1 {
        font-size: 2.8vmax;
    }

    .detailBlock-3-1-1>input {
        font-size: 1.8vmax;
    }

    .detailBlock-3-1>button:last-child {
        font-size: 1.4vmax;
    }

    .detailBlock-3>p {
        font-size: 1.6vmax;
    }

    .detailBlock-4 {
        font-size: 2vmax;
    }

    .detailBlock-4>p {
        font-size: 1.5vmax;
    }

    .submitReview {
        font-size: 2vmax;
        width: 67%;
        padding: 1vmax 2vmax;
    }
}

@media (max-width: 426px) {
    .productDetails {
        flex-direction: column;
    }

    .productDetails-2 {
        align-items: baseline;
    }

    .CarouselImage {
        width: 325px;
    }

    .detailBlock-1>p {
        font-size: 2vmax;
    }

    .detailBlock-2>span {
        font-size: 2vmax;
    }

    .detailBlock-1>h2 {
        font-size: 4vmax;
    }

    .detailBlock-3>h1 {
        font-size: 3.8vmax;
    }

    .detailBlock-3>p {
        border-top: 1px solid rgba(0, 0, 0, 0.205);
        border-bottom: 1px solid rgba(0, 0, 0, 0.205);
        padding: 1vmax 0;
        color: rgba(0, 0, 0, 0.651);
        font: 400 2vmax "Roboto";
    }

    .detailBlock-3-1-1>input {
        font-size: 1.8vmax;
    }

    .detailBlock-3-1>button:last-child {
        font-size: 2vmax;
    }

    .detailBlock-4 {
        font-size: 2.2vmax;
    }

    .detailBlock-4>p {
        font-size: 2vmax;
    }

    .submitReview {
        font-size: 3vmax;
        width: 40%;
        padding: 1vmax 2vmax;
    }
    .reviewsHeading {
        font-size: 2.4vmax;
    }
}