.cartItemCard {
    display: flex;
    align-items: center;
    box-sizing: border-box;
}

.cartItemCard>img {
    width: 4vmax;
}

.cartItemCard>div {
    display: flex;
    flex-direction: column;
    margin: 0.3vmax 1vmax;
}

.cartItemCard>div>a {
    font: 300 1vmax cursive;
    color: rgba(24, 24, 24, 0.815);
    text-decoration: none;
}

.cartItemCard>div>span {
    font: 300 1vmax "Roboto";
    color: rgba(24, 24, 24, 0.815);
}

.cartItemCard>div>p {
    color: tomato;
    font: 100 0.9vmax "Roboto";
    cursor: pointer;
    margin: 0;
}

@media (max-width: 769px) {
    .cartItemCard>div>a {
        font-size: 2vmax;
    }

    .cartItemCard>div>span {
        font-size: 2vmax;
    }

    .cartItemCard>div>p {
        font-size: 1.8vmax;
    }
    .cartItemCard>img {
        width: 7vmax;
    }
    
    .cartInput>input {
        font-size: 2.5vmax;
    }
    .cartInput>button {
        padding: 0.8vmax;
    }
    .cartInput>input {
        font-size: 2vmax;
    }
    .cartSubtotal {
        font-size: 2vmax;
    }
    .cartGrossProfitBox {
        font-size: 2vmax;
    }
    .checkOutBtn>button {
        font-size: 1.8vmax;
    }

}
@media (max-width: 426px) {
    .cartItemCard>div>p {
        font-size:2.5vmax;
    }
    .cartItemCard>div>a {
        font-size: 3vmax;
    }
    .cartItemCard>div>span {
        font-size: 2.5vmax;
    }  
    .cartItemCard>img {
        width: 10vmax;
    }
    .cartInput>input {
        font-size: 2.5vmax;
    }
    .cartInput>button {
        padding: 1vmax;
    }
    .cartInput>input {
        padding: 1vmax;
        font-size: 2.5vmax;
    }
    .cartSubtotal {
        font-size: 2.2vmax;
    }
    .cartGrossProfitBox {
        font-size: 2.5vmax;
    }
    .checkOutBtn>button {
        font-size: 2.2vmax;
        width: 100%;
    }
}