.reviewCard{
    flex: none;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.226);
    border: 1px solid rgba(56, 56, 56, 0.116);
    width: 30vmax;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1vmax;
    padding: 3vmax;
}
.reviewCard > img{
    width: 5vmax;
}
.reviewCard > p{
    color: rgba(0, 0, 0, 0.836);
    font: 600 1vmax "Roboto";
}
.reviewCardComment{
    color: rgba(0, 0, 0, 0.445);
    font: 300 1vmax "cursive";
}
@media (max-width: 426px) {
    .reviewCard{
        width: 91%;
    }
    .reviewCard > p {
        font-size:3vmax;
    }
    .reviewCard > span {
        font-size:3vmax;
    }
}