.emptyCart {
    margin: auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 50vh;
}

.emptyCart>svg {
    font-size: 5vmax;
    color: tomato;
}

.emptyCart>p {
    font-size: 2vmax;
}

.emptyCart>a {
    background-color: rgb(51, 51, 51);
    color: white;
    border: none;
    padding: 1vmax 3vmax;
    cursor: pointer;
    font: 400 1vmax "Roboto";
    text-decoration: none;
}

.cartPage {
    padding: 4vmax;
}

.CartHeader {
    background-color: tomato;
    color: white;
    width: 90%;
    box-sizing: border-box;
    margin: auto;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
    font: 300 1vmax "Robotp";
}

.CartHeader>p {
    margin: 10px;
}

.CartHeader>p:last-child {
    text-align: end;
}

.cartContainer {
    width: 90%;
    height: auto;
    margin: auto;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
}

.cartInput {
    display: flex;
    align-items: center;
    height: 5vmax;
}

.cartInput>button {
    border: none;
    background-color: rgba(0, 0, 0, 0.616);
    cursor: pointer;
    color: white;
    transition: all 0.5s;
    padding: 0.5vmax;
}

.cartInput>button:hover {
    background-color: rgba(0, 0, 0, 767);
}

.cartInput>input {
    outline: none;
    text-align: center;
    border: none;
    padding: 0.5vmax;
    width: 1vmax;
    font: 400 1vmax "Roboto";
    color: rgba(0, 0, 0, 0.74);
}

.cartSubtotal {
    display: flex;
    padding: 0.5vmax;
    font: 300 1vmax cursive;
    justify-content: flex-end;
    color: rgba(0, 0, 0, 0.753);
    box-sizing: border-box;
    align-items: center;
}

.cartGrossProfit {
    display: grid;
    grid-template-columns: 2fr 1.2fr;
}

.cartGrossProfitBox {
    border-top: 3px solid tomato;
    margin: 1vmax 4vmax;
    box-sizing: border-box;
    padding: 2vmax 0;
    font: 300 1vmax "Roboto";
    display: flex;
    justify-content: space-between;
}

.checkOutBtn {
    display: flex;
    justify-content: flex-end;
}

.checkOutBtn>button {
    background-color: tomato;
    color: white;
    border: none;
    padding: 0.8vmax 4vmax;
    width: 50%;
    font: 300 1vmax "Roboto";
    margin: 1vmax 4vmax;
    cursor: pointer;
    border-radius: 30px;
    transition: all 0.5s;
}

.checkOutBtn>button:hover {
    background-color: rgb(204, 78, 56);
}

@media (max-width: 769px) {

    .emptyCart>svg {
        font-size: 5vmax;
    }

    .emptyCart>a {
        padding: 1.5vmax 3.5vmax;
        font-size: 1.5vmax;
    }

    .emptyCart>p {
        font-size: 2vmax;
    }

    .CartHeader {
        font-size: 1.8vmax;
    }

    .cartInput>input {
        font-size: 1.5vmax;
        width: 3vmax;
    }

    .cartSubtotal {
        font-size: 1.5vmax;
    }

    .cartGrossProfitBox {
        font-size: 1.5vmax;
    }

    .checkOutBtn>button {
        font-size: 1.5vmax;
    }
}

@media (max-width: 426px) {

    .emptyCart>svg {
        font-size: 6vmax;
    }

    .emptyCart>a {
        padding: 2vmax 4vmax;
        font-size: 2vmax;
    }

    .emptyCart>p {
        font-size: 3vmax;
    }

    .cartPage {
        padding: 0vmax;
    }

    .CartHeader {
        font-size: 2.8vmax;
    }

    .CartHeader {
        width: 100%;
    }

    .cartGrossProfit {
        display: block;
    }

    .checkOutBtn>button {
        padding: 1.8vmax 4vmax;
    }

    .cartInput>input {
        width: 3vmax;
    }

}