.banner {
    /* background-image: linear-gradient(to right, #2216e9, #ffffff); */
    background-image: url(../Assets//backgroud.jpg);
    height: 100vmin;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    color: white;
    background-size: cover;
    background-position: center;
}

.banner>h1 {
    margin: 5vmax;
    font: 600 2.5vmax "Roboto";
}

.banner>p {
    font: 300 1.4vmax "Licida Sans";
}

.banner>a>button {
    margin-bottom: 4vmax;
    cursor: pointer;
    background-color: white;
    border: 1px solid white;
    border-radius: 0;
    padding: 1vmax;
    width: 100px;
    transition: all 0.5s;
    font: 500 1vmax "Roboto";
}

.banner>a>button:hover {
    background-color: rgb(255, 255, 255, 0);
    color: white;
}


.homeHeading {
    font-family: Roboto;
    width: 20%;
    margin: 50px auto;
    text-align: center;
    padding: 10px;
    border-bottom: 1px solid rgba(21, 21, 21, 0.5);
    color: rgb(0, 0, 0, 0.7);
}

.container {
    display: flex;
    margin: 20px auto;
    flex-wrap: wrap;
    justify-content: center;
}


@media (max-width: 769px) {
   
    .banner>p {
        font-size: 2.4vmax;
    }

    .banner>h1 {
        margin: 2vmax;
        font-size: 3vmax;
    }
    .banner>a>button {
        font-size: 12px;
        width: 85px;
        margin-top: 35px;
    }
    .homeHeading {
        width: 25%;
    }
}
@media (max-width: 426px) {
    .banner {
        height: 99vmin;
    }
    .banner>p {
        font-size: 3.4vmax;
    }

    .banner>h1 {
        margin: 2vmax;
        font-size: 3vmax;
    }
    
    .banner>a>button {
        font-size: 12px;
        width: 85px;
        margin-top: 15px;
    }
    .banner::after{
        height: 112vmin;
    }
    .homeHeading {
        width: 45%;
        font-size: 18px;
    }
   
}